<template>
  <view class="container">
    <!-- Êú™ÁôªÂΩïÁä∂ÊÄÅ -->
    <view v-if="!isLoggedIn" class="login-required">
      <view class="login-card">
        <view class="login-icon">üîí</view>
        <text class="login-title">ËØ∑ÂÖàÁôªÂΩï</text>
        <text class="login-desc">ÁôªÂΩïÂêéÂç≥ÂèØÊü•ÁúãÂíåÁÆ°ÁêÜÊÇ®ÁöÑÊ∂àÊÅØ</text>
        <button class="login-btn" @tap="goToLogin">
          <text class="login-btn-text">Á´ãÂç≥ÁôªÂΩï</text>
        </button>
      </view>
    </view>

    <!-- Â∑≤ÁôªÂΩïÁä∂ÊÄÅ - ÊòæÁ§∫Ê∂àÊÅØÁïåÈù¢ -->
    <view v-else class="message-container">
      <!-- Ëá™ÂÆö‰πâÂØºËà™Ê†è -->
      <view class="custom-navbar">
        <view class="status-bar" :style="{ height: statusBarHeight + 'px' }"></view>
        <view class="nav-content">
          <text class="nav-title">Ê∂àÊÅØ</text>
        </view>
      </view>
      
      <!-- Âõ∫ÂÆöÁ≠õÈÄâÊ†áÁ≠æ -->
      <view class="filter-tabs-fixed" :style="{ top: statusBarHeight + 44 + 'px' }">
        <view class="tabs-container">
          <view 
            class="filter-tab" 
            :class="{ active: activeTab === 'all' }"
            @tap="switchTab('all')"
          >
            <text class="tab-text">ÂÖ®ÈÉ®</text>
            <view class="tab-badge" v-if="totalUnreadCount > 0">{{ totalUnreadCount > 99 ? '99+' : totalUnreadCount }}</view>
          </view>
          <view 
            class="filter-tab" 
            :class="{ active: activeTab === 'system' }"
            @tap="switchTab('system')"
          >
            <text class="tab-text">Á≥ªÁªüÊ∂àÊÅØ</text>
            <view class="tab-badge" v-if="systemUnreadCount > 0">{{ systemUnreadCount > 99 ? '99+' : systemUnreadCount }}</view>
          </view>
          <view 
            class="filter-tab" 
            :class="{ active: activeTab === 'group' }"
            @tap="switchTab('group')"
          >
            <text class="tab-text">Áæ§ÁªÑÊ∂àÊÅØ</text>
            <view class="tab-badge" v-if="groupUnreadCount > 0">{{ groupUnreadCount > 99 ? '99+' : groupUnreadCount }}</view>
          </view>
        </view>
        
        <!-- ‰∏ÄÈîÆÂ∑≤ËØªÊåâÈíÆ -->
        <view 
          class="mark-all-read-btn" 
          @tap="markAllAsRead"
          v-if="getCurrentUnreadCount() > 0"
        >
          <text class="read-btn-icon">‚úì</text>
          <text class="read-btn-text">Â∑≤ËØª</text>
        </view>
      </view>
      
      <!-- Ê∂àÊÅØÂàóË°® -->
      <scroll-view
        class="message-list"
        :style="{ marginTop: statusBarHeight + 44 + 56 + 'px' }"
        scroll-y="true"
        enable-back-to-top="true"
        :bounces="false"
        @scrolltolower="loadMoreMessages"
        :refresher-enabled="true"
        :refresher-triggered="isRefreshing"
        @refresherrefresh="handleRefresh"
        @refresherrestore="handleRefreshRestore"
      >
        <view class="message-list-content">
          <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
          <view v-if="isLoading && !hasLoadedOnce" class="loading-state">
            <view class="loading-spinner"></view>
            <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
          </view>
          
          <!-- Á≥ªÁªüÊ∂àÊÅØÂç°ÁâáÂàóË°® - ‰ªÖÂú®ÂÖ®ÈÉ®ÊàñÁ≥ªÁªüÊ∂àÊÅØÊ†áÁ≠æ‰∏ãÊòæÁ§∫ -->
          <template v-if="activeTab === 'all' || activeTab === 'system'">
            <MessageCard
              v-for="msg in systemMessages"
              :key="msg.event_id"
              :message="msg"
              :message-type="'system'"
              :loading="isLoading"
              @tap="handleMessageTap"
              @markAsRead="handleMarkAsRead"
            />
          </template>
          
          <!-- Áæ§ÁªÑÊ∂àÊÅØÂç°ÁâáÂàóË°® - ‰ªÖÂú®ÂÖ®ÈÉ®ÊàñÁæ§ÁªÑÊ∂àÊÅØÊ†áÁ≠æ‰∏ãÊòæÁ§∫ -->
          <template v-if="activeTab === 'all' || activeTab === 'group'">
            <MessageCard
              v-for="msg in groupMessages"
              :key="msg.event_id"
              :message="msg"
              :message-type="'group'"
              :loading="isLoading"
              @tap="handleMessageTap"
              @markAsRead="handleMarkAsRead"
            />
          </template>
          
          <!-- Á©∫Áä∂ÊÄÅ -->
          <view v-if="!isLoading && shouldShowEmpty" class="empty-state">
            <view class="empty-animation">
              <view class="empty-icon">üí¨</view>
              <view class="empty-waves">
                <view class="wave wave1"></view>
                <view class="wave wave2"></view>
                <view class="wave wave3"></view>
              </view>
            </view>
            <text class="empty-title">{{ getEmptyTitle() }}</text>
            <text class="empty-desc">{{ getEmptyDesc() }}</text>
          </view>
          
          <!-- Â∫ïÈÉ®Âä†ËΩΩÊõ¥Â§ö -->
          <view v-if="isLoadingMore" class="load-more">
            <view class="loading-spinner small"></view>
            <text class="load-more-text">Âä†ËΩΩÊõ¥Â§ö...</text>
          </view>
        </view>
      </scroll-view>
    </view>
  </view>
</template>

<script setup>
import { ref, computed, onMounted, watch, nextTick } from 'vue'
import { useInfoStore } from '@/store/Info.js'
import MessageCard from '@/components/MessageCard/MessageCard.vue'
import { useMesstore } from '@/store/mes.js'
import { onLoad, onShow, onPullDownRefresh } from '@dcloudio/uni-app'

// Ëé∑ÂèñÁä∂ÊÄÅÁÆ°ÁêÜ
const userStore = useInfoStore()
const mesStore = useMesstore()

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const statusBarHeight = ref(0)
const activeTab = ref('all')
const isLoading = ref(false)
const isRefreshing = ref(false)
const isLoadingMore = ref(false)
const hasLoadedOnce = ref(false)

// ÁôªÂΩïÁä∂ÊÄÅËÆ°ÁÆóÂ±ûÊÄß
const isLoggedIn = computed(() => userStore.signal)

// Êú™ËØªÊï∞ÈáèËÆ°ÁÆóÂ±ûÊÄß
const totalUnreadCount = computed(() => mesStore.totalUnreadCount)
const systemUnreadCount = computed(() => mesStore.systemUnreadCount)
const groupUnreadCount = computed(() => mesStore.groupUnreadCount)

// Á≥ªÁªüÊ∂àÊÅØÂàóË°®
const systemMessages = computed(() => {
  if (!isLoggedIn.value) return []
  return mesStore.systemmes || []
})

// Áæ§ÁªÑÊ∂àÊÅØÂàóË°®  
const groupMessages = computed(() => {
  if (!isLoggedIn.value) return []
  return mesStore.groupmes || []
})

// ÊòØÂê¶ÊòæÁ§∫Á©∫Áä∂ÊÄÅ
const shouldShowEmpty = computed(() => {
  if (activeTab.value === 'all') {
    return systemMessages.value.length === 0 && groupMessages.value.length === 0
  } else if (activeTab.value === 'system') {
    return systemMessages.value.length === 0
  } else if (activeTab.value === 'group') {
    return groupMessages.value.length === 0
  }
  return false
})

// ÁîüÂëΩÂë®Êúü
onMounted(async () => {
  try {
    const sysInfo = uni.getSystemInfoSync()
    statusBarHeight.value = sysInfo.statusBarHeight || 0
  } catch (error) {
    console.error('Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÂ§±Ë¥•:', error)
    statusBarHeight.value = 20
  }
})

onShow(async () => {
  if (isLoggedIn.value) {
    await loadUserMessages()
  }
})

onLoad(async () => {
  if (isLoggedIn.value) {
    await loadUserMessages()
  }
})

// ‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(async () => {
  await handleRefresh()
  uni.stopPullDownRefresh()
})

// ÁõëÂê¨ÁôªÂΩïÁä∂ÊÄÅÂèòÂåñ
watch(isLoggedIn, async (newVal) => {
  if (newVal && !hasLoadedOnce.value) {
    console.log('Áî®Êà∑Â∑≤ÁôªÂΩïÔºåÂä†ËΩΩÊ∂àÊÅØÊï∞ÊçÆ')
    await loadUserMessages()
  } else if (!newVal) {
    console.log('Áî®Êà∑Â∑≤ÁôªÂá∫ÔºåÊ∏ÖÁ©∫Ê∂àÊÅØÊï∞ÊçÆ')
    mesStore.clearAllMessages()
    hasLoadedOnce.value = false
  }
})

// Ëé∑ÂèñÂΩìÂâçÊ†áÁ≠æÁöÑÊú™ËØªÊï∞Èáè
const getCurrentUnreadCount = () => {
  if (activeTab.value === 'all') {
    return totalUnreadCount.value
  } else if (activeTab.value === 'system') {
    return systemUnreadCount.value
  } else if (activeTab.value === 'group') {
    return groupUnreadCount.value
  }
  return 0
}

// ÁôªÂΩïÁõ∏ÂÖ≥ÊñπÊ≥ï
const goToLogin = () => {
  uni.switchTab({
    url: '../mymessage/mymessage'
  })
}

// Âä†ËΩΩÁî®Êà∑Ê∂àÊÅØÊï∞ÊçÆ
const loadUserMessages = async (isRefresh = false) => {
  if (!isLoggedIn.value) return
  
  if (isRefresh) {
    isRefreshing.value = true
  } else if (!hasLoadedOnce.value) {
    isLoading.value = true
  }
  
  try {
    console.log('ÂºÄÂßãÂä†ËΩΩÁî®Êà∑Ê∂àÊÅØÊï∞ÊçÆ...')
    const result = await mesStore.getsystem(isRefresh)
    
    if (result && result.success) {
      console.log('Ê∂àÊÅØÊï∞ÊçÆÂä†ËΩΩÊàêÂäü')
      hasLoadedOnce.value = true
    }
    
    await nextTick()
  } catch (error) {
    console.error('Âä†ËΩΩÊ∂àÊÅØÂ§±Ë¥•:', error)
    
    // Ê†πÊçÆÈîôËØØÁ±ªÂûãÊòæÁ§∫‰∏çÂêåÊèêÁ§∫
    let errorMsg = 'Âä†ËΩΩÊ∂àÊÅØÂ§±Ë¥•'
    if (error.message && error.message.includes('ÁΩëÁªú')) {
      errorMsg = 'ÁΩëÁªúËøûÊé•ÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆ'
    } else if (error.message && error.message.includes('ÁôªÂΩï')) {
      errorMsg = 'ÁôªÂΩïÁä∂ÊÄÅÂºÇÂ∏∏ÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï'
    }
    
    uni.showToast({
      title: errorMsg,
      icon: 'error',
      duration: 2000
    })
  } finally {
    isLoading.value = false
    isRefreshing.value = false
  }
}

// Â§ÑÁêÜÂà∑Êñ∞
const handleRefresh = async () => {
  if (isRefreshing.value) return
  
  isRefreshing.value = true
  try {
    await loadUserMessages(true)
    uni.showToast({
      title: 'Âà∑Êñ∞ÊàêÂäü',
      icon: 'success',
      duration: 1500
    })
  } catch (error) {
    console.error('Âà∑Êñ∞Â§±Ë¥•:', error)
    uni.showToast({
      title: 'Âà∑Êñ∞Â§±Ë¥•',
      icon: 'error',
      duration: 1500
    })
  } finally {
    isRefreshing.value = false
  }
}

// Â§ÑÁêÜÂà∑Êñ∞ÊÅ¢Â§ç
const handleRefreshRestore = () => {
  isRefreshing.value = false
}

// Âä†ËΩΩÊõ¥Â§öÊ∂àÊÅØÔºà‰∏äÊãâÂä†ËΩΩÔºâ
const loadMoreMessages = async () => {
  if (isLoading.value || isRefreshing.value || isLoadingMore.value) return
  
  console.log('Ëß¶ÂèëÂä†ËΩΩÊõ¥Â§öÊ∂àÊÅØ')
  isLoadingMore.value = true
  
  try {
    // ËøôÈáåÂèØ‰ª•ÂÆûÁé∞ÂàÜÈ°µÂä†ËΩΩÈÄªËæë
    // ÊöÇÊó∂Âè™ÊòØÂª∂Ëøü‰∏Ä‰∏ãÔºåÈÅøÂÖçÈ¢ëÁπÅËß¶Âèë
    await new Promise(resolve => setTimeout(resolve, 1000))
  } catch (error) {
    console.error('Âä†ËΩΩÊõ¥Â§öÂ§±Ë¥•:', error)
  } finally {
    isLoadingMore.value = false
  }
}

// ÂàáÊç¢Ê†áÁ≠æÈ°µ
const switchTab = (tab) => {
  if (activeTab.value === tab) return
  activeTab.value = tab
  console.log('ÂàáÊç¢Âà∞Ê†áÁ≠æÈ°µ:', tab)
}

// Â§ÑÁêÜÊ∂àÊÅØÂç°ÁâáÁÇπÂáª‰∫ã‰ª∂
const handleMessageTap = (msg, messageType) => {
  if (!isLoggedIn.value || !msg) return
  
  console.log('ÁÇπÂáªÊ∂àÊÅØÂç°Áâá:', msg, 'Ê∂àÊÅØÁ±ªÂûã:', messageType)
  
  try {
    // Ê†πÊçÆÊ∂àÊÅØÁ±ªÂûãË∑≥ËΩ¨Âà∞Áõ∏Â∫îÁöÑËØ¶ÊÉÖÈ°µÈù¢
    if (messageType === 'system') {
      console.log("Á≥ªÁªüÊ∂àÊÅØË∑≥ËΩ¨")
      uni.navigateTo({
        url: `/pages/detail/ChatSystem`
      })
    } else if (messageType === 'group') {
      console.log("Áæ§ÁªÑÊ∂àÊÅØË∑≥ËΩ¨")
      uni.navigateTo({
        url: `/pages/detail/ChatGroup?id=${msg.msg_group_id}&groupName=${encodeURIComponent(msg.group_name || 'Áæ§ÁªÑÊ∂àÊÅØ')}`
      })
    }
  } catch (error) {
    console.error('È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•:', error)
    uni.showToast({
      title: 'È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•',
      icon: 'error',
      duration: 1500
    })
  }
}

// Â§ÑÁêÜÂçï‰∏™Ê∂àÊÅØÊ†áËÆ∞Â∑≤ËØª
const handleMarkAsRead = async (msg, messageType) => {
  // ‰ΩøÁî®Êñ∞ÁöÑÊú™ËØªÂà§Êñ≠ÈÄªËæë
  if (!msg || !mesStore.isMessageUnread(msg)) return
  
  try {
    console.log('Ê†áËÆ∞Ê∂àÊÅØÂ∑≤ËØªID:', msg.event_id, 'Ê∂àÊÅØÁ±ªÂûã:', messageType)
    
    if (messageType === 'system') {
      // await mesStore.markSystemMessageAsRead(msg.id)
    } else {
      // await mesStore.markGroupMessageAsRead(msg.id)
    }
  } catch (error) {
    console.error('Ê†áËÆ∞Ê∂àÊÅØÂ∑≤ËØªÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'Êìç‰ΩúÂ§±Ë¥•',
      icon: 'error',
      duration: 1500
    })
  }
}

// Ëé∑ÂèñÁ©∫Áä∂ÊÄÅÊ†áÈ¢ò
const getEmptyTitle = () => {
  const titles = {
    all: 'ÊöÇÊó†Ê∂àÊÅØ',
    system: 'ÊöÇÊó†Á≥ªÁªüÊ∂àÊÅØ',
    group: 'ÊöÇÊó†Áæ§ÁªÑÊ∂àÊÅØ'
  }
  return titles[activeTab.value] || 'ÊöÇÊó†Ê∂àÊÅØ'
}

// Ëé∑ÂèñÁ©∫Áä∂ÊÄÅÊèèËø∞
const getEmptyDesc = () => {
  const descs = {
    all: 'ÁõÆÂâçËøòÊ≤°Êúâ‰ªª‰ΩïÊ∂àÊÅØ',
    system: 'ÊöÇÊó∂Ê≤°ÊúâÁ≥ªÁªüÈÄöÁü•',
    group: 'ÊÇ®ËøòÊú™Âä†ÂÖ•‰ªª‰ΩïÁæ§ÁªÑ'
  }
  return descs[activeTab.value] || 'ÊöÇÊó†ÂÜÖÂÆπ'
}

// ‰∏ÄÈîÆÊ†áËÆ∞Â∑≤ËØª
const markAllAsRead = async () => {
  if (!isLoggedIn.value) return
  
  let unreadMessages = []
  
  // Ê†πÊçÆÂΩìÂâçÊ†áÁ≠æÈ°µËé∑ÂèñÊú™ËØªÊ∂àÊÅØ
  if (activeTab.value === 'all') {
    const systemUnread = systemMessages.value.filter(msg => mesStore.isMessageUnread(msg))
    const groupUnread = groupMessages.value.filter(msg => mesStore.isMessageUnread(msg))
    unreadMessages = [...systemUnread, ...groupUnread]
  } else if (activeTab.value === 'system') {
    unreadMessages = systemMessages.value.filter(msg => mesStore.isMessageUnread(msg))
  } else if (activeTab.value === 'group') {
    unreadMessages = groupMessages.value.filter(msg => mesStore.isMessageUnread(msg))
  }
  
  if (unreadMessages.length === 0) {
    uni.showToast({
      title: 'Â∑≤ÁªèÊ≤°ÊúâÊú™ËØªÊ∂àÊÅØ‰∫Ü',
      icon: 'none',
      duration: 1500
    })
    return
  }
  
  try {
    // ÊòæÁ§∫Á°ÆËÆ§ÂØπËØùÊ°Ü
    const res = await new Promise((resolve) => {
      uni.showModal({
        title: 'Á°ÆËÆ§Êìç‰Ωú',
        content: `Á°ÆÂÆöË¶ÅÂ∞Ü${unreadMessages.length}Êù°Êú™ËØªÊ∂àÊÅØÊ†áËÆ∞‰∏∫Â∑≤ËØªÂêóÔºü`,
        success: resolve
      })
    })
    
    if (!res.confirm) return
    
    // ÊòæÁ§∫Âä†ËΩΩÊèêÁ§∫
    uni.showLoading({
      title: 'Â§ÑÁêÜ‰∏≠...',
      mask: true
    })
    
    const promises = []
    
    // ÂàÜÂà´Â§ÑÁêÜÁ≥ªÁªüÊ∂àÊÅØÂíåÁæ§ÁªÑÊ∂àÊÅØ
    unreadMessages.forEach(msg => {
      if (msg.type === 'system') {
        promises.push(mesStore.markSystemMessageAsRead(msg.id))
      } else {
        promises.push(mesStore.markGroupMessageAsRead(msg.id))
      }
    })
    
    await Promise.allSettled(promises)
    
    uni.hideLoading()
    uni.showToast({
      title: `Â∑≤Ê†áËÆ∞${unreadMessages.length}Êù°Ê∂àÊÅØ‰∏∫Â∑≤ËØª`,
      icon: 'success',
      duration: 2000
    })
    
  } catch (error) {
    console.error('ÊâπÈáèÊ†áËÆ∞Â∑≤ËØªÂ§±Ë¥•:', error)
    uni.hideLoading()
    uni.showToast({
      title: 'Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
      icon: 'error',
      duration: 2000
    })
  }
}
</script>

